.at-button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  padding: 0 0.68267rem;
  height: 1.96267rem;
  color: #333;
  font-size: 0.68267rem;
  line-height: 1.92rem;
  text-align: center;
  border-radius: 0.17067rem;
  border: 1PX solid #C5D9E8;
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.at-button:active {
  opacity: 0.6;
}

.at-button--active {
  opacity: 0.6;
}

.at-button--disabled {
  opacity: 0.3;
}

.at-button--disabled:active {
  opacity: 0.3;
}

.at-button--primary {
  background-color: #5c89e4;
  color: #fff;
  border: 1PX solid #5c89e4;
}

.at-button--secondary {
  background-color: #fff;
  color: #5c89e4;
  border: 1PX solid #5c89e4;
}

.at-button__icon {
  margin: 2PX 0.42667rem 0 0.42667rem;
  display: inline-block;
}

.at-button--circle {
  border-radius: 2.13333rem;
  overflow: hidden;
  background-clip: border-box;
}

.at-button__text {
  display: inline;
}

.at-button--small {
  display: inline-block;
  padding: 0 0.34133rem;
  width: auto;
  min-width: 2.13333rem;
  max-width: 15.14667rem;
  height: 1.28rem;
  font-size: 0.59733rem;
  line-height: 1.23733rem;
}

