/**
 * CSS cubic-bezier timing functions
 * http://bourbon.io/docs/#timing-functions
 */

@-webkit-keyframes progress-active {
  from {
    opacity: 0.5;
    -webkit-transform: translate(-100%);
            transform: translate(-100%);
  }

  to {
    opacity: 0.1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@keyframes progress-active {
  from {
    opacity: 0.5;
    -webkit-transform: translate(-100%);
            transform: translate(-100%);
  }

  to {
    opacity: 0.1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

.at-progress {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.at-progress__outer {
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  margin-right: 0;
  padding-right: 0;
}

.at-progress__outer-inner {
  width: 100%;
  background-color: #F7F7F7;
  border-radius: 2.13333rem;
  vertical-align: middle;
}

.at-progress__outer-inner-background {
  height: 0.34133rem;
  border-radius: 2.13333rem;
  background-color: #78A4F4;
  -webkit-transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
  transition: all 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
  position: relative;
  overflow: hidden;
}

.at-progress__content {
  -webkit-flex: 1 1 1.49333rem;
          flex: 1 1 1.49333rem;
  font-size: 0.512rem;
  margin-left: 0.21333rem;
  text-align: center;
}

.at-progress--progress .at-progress__outer-inner-background::before {
  content: "";
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  border-radius: 2.13333rem;
  -webkit-animation: progress-active 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
          animation: progress-active 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}

